import { CircleStencil, Cropper, Preview, RectangleStencil } from 'vue-advanced-cropper';
import 'vue-advanced-cropper/dist/style.css';
import 'vue-advanced-cropper/dist/theme.bubble.css';
export default {
  name: 'ImagePicker',
  components: {
    Cropper,
    Preview,
    // eslint-disable-next-line vue/no-unused-components
    CircleStencil,
    // eslint-disable-next-line vue/no-unused-components
    RectangleStencil
  },
  inheritAttrs: false,
  props: {
    cropperAutoZoom: {
      type: Boolean,
      default: true
    },
    cropperScalable: {
      type: Boolean,
      default: false
    },
    cropperMovable: {
      type: Boolean,
      default: false
    },
    cropperRatio: {
      type: Number,
      default: null
    },
    roundPreview: {
      type: Boolean,
      default: false
    },
    previewWidth: {
      type: Number,
      default: null
    },
    previewHeight: {
      type: Number,
      default: 120
    }
  },
  emits: ['croppedFileChange', 'selectedFileChange', 'save', 'cancel'],
  data() {
    return {
      secondaryColor: this.secondaryColor,
      files: [],
      result: {
        coordinates: null,
        image: null
      }
    };
  },
  computed: {
    calcPreviewWidth() {
      if (this.previewWidth) return this.previewWidth;
      if (this.result && this.result.coordinates) return this.result.coordinates.width / this.result.coordinates.height * this.previewHeight;else return this.previewHeight;
    }
  },
  watch: {
    files: {
      handler(val, oldVal) {
        this.$emit('selectedFileChange', val[0] || null);
      }
    }
  },
  methods: {
    onChange({
      coordinates,
      image
    }) {
      this.result = {
        coordinates,
        image
      };
      const {
        canvas
      } = this.$refs.cropper.getResult();
      this.$emit('croppedFileChange', this.result, canvas.toDataURL());
    },
    inputFilter(newFile, oldFile, prevent) {
      if (newFile && !oldFile) {
        if (!/\.(gif|jpg|jpeg|png|webp)$/i.test(newFile.name)) {
          this.$toast.error(this.$t('invalidFile'));
          return prevent();
        }
      }
      if (newFile && (!oldFile || newFile.file !== oldFile.file)) {
        newFile.url = '';
        const URL = window.URL || window.webkitURL;
        if (URL && URL.createObjectURL) {
          newFile.url = URL.createObjectURL(newFile.file);
        }
      }
    },
    flip(x, y) {
      if (this.$refs.cropper.imageTransforms.rotate % 180 !== 0) {
        this.$refs.cropper.flip(!x, !y);
      } else {
        this.$refs.cropper.flip(x, y);
      }
    },
    rotate(angle) {
      this.$refs.cropper.rotate(angle);
    },
    beforeImageUpload(file) {
      const isImg = file.type === 'image/jpeg' || 'image/jpg' || 'image/gif' || 'image/png' || 'image/webp';
      const sizeLessThan5M = file.size / 1024 / 1024 < 5;
      if (!isImg) this.$message.error(this.$t('uploadImageFormatWarning'));
      if (!sizeLessThan5M) this.$message.error(this.$t('uploadFileSizeWarning', [5]));
      return isImg && sizeLessThan5M;
    },
    selectedFileChange(file, fileList) {
      var reader = new FileReader();
      reader.readAsDataURL(file.raw);
      reader.onload = e => {
        this.files = [this.result];
      };
    },
    cancel() {
      if (!this.files.length) this.$emit('cancel');
      this.files = [];
    },
    save() {
      const {
        canvas
      } = this.$refs.cropper.getResult();
      this.$emit('save', this.result, canvas.toDataURL());
    }
  }
};