import AccessManager from '@/mixins/access-manager';
import ViewsBaseMixin from '@/mixins/views-base';
export default {
  name: 'Modules',
  mixins: [ViewsBaseMixin, AccessManager],
  data() {
    return {
      newsletterEmail: '',
      subscriptionModule: false,
      displayPromotions: false,
      newsletterModule: false,
      eCommerceModule: false,
      newsModule: false,
      adModule: false,
      currency: 0,
      parent: this.$parent.$parent.$parent
    };
  },
  created() {
    this.init();
  },
  methods: {
    async init() {
      this.newsletterEmail = this.parent.getSettingValue(this.parent.Settings.NEWSLETTER_EMAIL.NAME);
      this.currency = this.parent.getSettingValue(this.parent.Settings.WEBSITE_CURRENCY.NAME, this.parent.VarTypes.NUMBER);
      this.displayPromotions = !!this.parent.getSettingValue(this.parent.Settings.DISPLAY_PROMOTIONS.NAME, this.parent.VarTypes.NUMBER);
      this.adModule = !!this.parent.getSettingValue(this.parent.Settings.AD_MODULE.NAME, this.parent.VarTypes.NUMBER);
      this.newsModule = !!this.parent.getSettingValue(this.parent.Settings.NEWS_MODULE.NAME, this.parent.VarTypes.NUMBER);
      this.eCommerceModule = !!this.parent.getSettingValue(this.parent.Settings.E_COMMERCE_MODULE.NAME, this.parent.VarTypes.NUMBER);
      this.newsletterModule = !!this.parent.getSettingValue(this.parent.Settings.NEWSLETTER_MODULE.NAME, this.parent.VarTypes.NUMBER);
      this.subscriptionModule = !!this.parent.getSettingValue(this.parent.Settings.SUBSCRIPTION_MODULE.NAME, this.parent.VarTypes.NUMBER);
    },
    async updateNewsletterEmail(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.NEWSLETTER_EMAIL.NAME,
        value: value
      });
      await this.parent.getAllSettings();
    },
    async updateCurrency(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.WEBSITE_CURRENCY.NAME,
        value: value
      });
      await this.parent.getAllSettings();
    },
    async updateModuleStatus(value, name) {
      await this.parent.updateSetting({
        name: name,
        value: value ? 1 : 0
      });
      await this.parent.getAllSettings();
    }
  }
};