import "core-js/modules/es.array.push.js";
import AccessManager from '@/mixins/access-manager';
import ViewsBaseMixin from '@/mixins/views-base';
export default {
  name: 'General',
  mixins: [ViewsBaseMixin, AccessManager],
  data() {
    return {
      entries: [],
      dateFormat: '',
      adminLanguage: '',
      parent: this.$parent.$parent.$parent
    };
  },
  watch: {
    entries: {
      handler(val, oldVal) {
        this.updateLangList();
      },
      deep: true
    }
  },
  created() {
    this.init();
  },
  methods: {
    async init() {
      this.adminLanguage = this.$cookie.isCookieAvailable(this.parent.constants.LANG_COOKIE) ? this.$cookie.getCookie(this.parent.constants.LANG_COOKIE) : process.env.VUE_APP_I18N_FALLBACK_LOCALE;
      this.dateFormat = this.parent.getSettingValue(this.parent.Settings.DATE_FORMAT.NAME);
      try {
        this.entries = JSON.parse(this.parent.getSettingValue(this.parent.Settings.LANG_LIST.NAME));
      } catch (e) {
        this.entries = [{
          code: 'FR',
          label: 'Français'
        }];
      }
    },
    updateAdminLanguage() {
      this.$i18n.locale = this.adminLanguage;
      this.$cookie.setCookie(this.parent.constants.LANG_COOKIE, this.adminLanguage, {
        expire: Infinity
      });
      location.reload();
    },
    async updateDateFormat() {
      await this.parent.updateSetting({
        name: this.parent.Settings.DATE_FORMAT.NAME,
        value: this.dateFormat
      });
      await this.parent.getAllSettings();
    },
    async updateLangList() {
      await this.parent.updateSetting({
        name: this.parent.Settings.LANG_LIST.NAME,
        value: JSON.stringify(this.entries.filter(e => e.label && e.code))
      });
      await this.parent.getAllSettings();
    },
    addEntry() {
      this.entries.push({
        label: '',
        code: ''
      });
    },
    removeEntry(index) {
      this.entries.splice(index, 1);
    },
    fixLangCode(v, i) {
      this.entries[i].code = v.toUpperCase().substring(0, 2);
    }
  }
};