import AccessManager from '@/mixins/access-manager';
import ViewsBaseMixin from '@/mixins/views-base';
export default {
  name: 'Notifications',
  mixins: [ViewsBaseMixin, AccessManager],
  data() {
    return {
      enableEmailNotifications: false,
      enableAdminNotifications: false,
      parent: this.$parent.$parent.$parent
    };
  },
  created() {
    this.init();
  },
  methods: {
    async init() {
      this.enableEmailNotifications = !!this.parent.getSettingValue(this.parent.Settings.ENABLE_EMAIL_NOTIFICATIONS.NAME, this.parent.VarTypes.NUMBER);
      this.enableAdminNotifications = !!this.parent.getSettingValue(this.parent.Settings.ENABLE_ADMIN_NOTIFICATIONS.NAME, this.parent.VarTypes.NUMBER);
    },
    async updateEmailNotifications(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.ENABLE_EMAIL_NOTIFICATIONS.NAME,
        value: value ? 1 : 0
      });
      await this.parent.getAllSettings();
    },
    async updateAdminNotifications(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.ENABLE_ADMIN_NOTIFICATIONS.NAME,
        value: value ? 1 : 0
      });
      await this.parent.getAllSettings();
    }
  }
};