import { useTemplateStore } from '@/stores/template';
import { mapWritableState } from 'pinia';
import Sections from './Sections';
import Widgets from './Widgets/';
export default {
  name: 'UiEditor',
  components: {
    Sections,
    Widgets
  },
  data() {
    return {
      index: 0,
      jsonData: {},
      oldJsonData: {},
      currentData: {},
      inputsNumber: 0,
      isLoading: false,
      dataHasChanged: false,
      parent: this.$parent.$parent.$parent
    };
  },
  computed: {
    ...mapWritableState(useTemplateStore, ['uiEditorIndex'])
  },
  async created() {
    this.index = this.uiEditorIndex;
    this.isLoading = true;
    await this.loadJSONData();
    this.isLoading = false;
    this.jsonData = this.index ? this.widgetsJSONData : this.sectionsJSONData;
    this.oldJsonData = this.$_.cloneDeep(this.jsonData);
  },
  methods: {
    switchJSONData(i) {
      if (i) {
        if (this.dataHasChanged) this.confirmSwitching(this.widgetsJSONData, i);else this.updateContent(this.widgetsJSONData, i);
      } else {
        if (this.dataHasChanged) this.confirmSwitching(this.sectionsJSONData, i);else this.updateContent(this.sectionsJSONData, i);
      }
    },
    confirmSwitching(data, i) {
      this.$confirm(this.$t('discardChanges'), this.$t('confirm'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        this.updateContent(data, i);
      }).catch(() => {
        /* empty */
      });
    },
    updateContent(data, i) {
      this.jsonData = data;
      this.dataHasChanged = false;
      this.index = i;
      this.uiEditorIndex = i;
    },
    updateJSONData(dataHasChanged, length, data, filteredData) {
      this.dataHasChanged = dataHasChanged;
      this.currentData = filteredData;
      this.inputsNumber = length;
    },
    async saveJSONData() {
      let dataNumber = 0;
      this.$_.forIn(this.currentData, (val, key) => {
        dataNumber++;
      });
      if (this.inputsNumber != dataNumber) {
        this.$message.warning(this.$t('pleaseFillInAllFields'));
        return false;
      }
      const task = await this.parent.updateJSON({
        data: this.formatDataKeys(this.currentData),
        file: this.index ? 'widgets' : 'sections'
      });
      if (task.success && task.data) {
        await this.loadJSONData();
        this.jsonData = this.index ? this.widgetsJSONData : this.sectionsJSONData;
        this.oldJsonData = this.$_.cloneDeep(this.jsonData);
        this.$message.success(this.$t('success'));
      } else {
        this.$message.error(this.$t('anErrorOccured'));
      }
    },
    formatDataKeys(data) {
      const result = {};
      this.$_.forIn(data, (value, key) => {
        const clone = this.$_.cloneDeep(value);
        if (this.index) {
          clone.wid = this.$_.snakeCase(key);
          result[clone.wid] = clone;
        } else {
          clone.sid = this.$_.snakeCase(key);
          result[clone.sid] = clone;
        }
      });
      return result;
    }
  }
};