import AccessManager from '@/mixins/access-manager';
import ViewsBaseMixin from '@/mixins/views-base';
import sanitizer from 'string-sanitizer';
export default {
  name: 'Advanced',
  mixins: [ViewsBaseMixin, AccessManager],
  data() {
    return {
      adminFolder: '',
      googleMapsApiKey: '',
      gmailApiSettings: {},
      discourageIndexing: false,
      parent: this.$parent.$parent.$parent
    };
  },
  watch: {
    adminFolder: 'sanitizeFolderName'
  },
  created() {
    this.init();
  },
  methods: {
    async init() {
      this.adminFolder = this.parent.getSettingValue(this.parent.Settings.ADMIN_FOLDER.NAME);
      this.googleMapsApiKey = this.parent.getSettingValue(this.parent.Settings.GOOGLE_MAPS_API_KEY.NAME);
      try {
        this.gmailApiSettings = JSON.parse(this.parent.getSettingValue(this.parent.Settings.GMAIL_API_SETTINGS.NAME));
      } catch (e) {
        this.gmailApiSettings = {
          username: '',
          clientId: '',
          clientSecret: '',
          refreshToken: ''
        };
      }
      this.discourageIndexing = this.parent.getSettingValue(this.parent.Settings.DISCOURAGE_INDEXING.NAME, this.parent.VarTypes.BOOLEAN);
    },
    sanitizeFolderName() {
      this.adminFolder = sanitizer.sanitize.addUnderscore(this.adminFolder.toLowerCase());
    },
    async updateGMapsApiKey(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.GOOGLE_MAPS_API_KEY.NAME,
        value: value
      });
      await this.parent.getAllSettings();
    },
    async updateGmailApiSettings() {
      await this.parent.updateSetting({
        name: this.parent.Settings.GMAIL_API_SETTINGS.NAME,
        value: JSON.stringify(this.gmailApiSettings)
      });
      await this.parent.getAllSettings();
    },
    async updateDiscourageIndexing(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.DISCOURAGE_INDEXING.NAME,
        value: value ? 1 : 0
      });
      await this.parent.getAllSettings();
    },
    async updateAdminFolder() {
      const currentFolder = this.parent.getSettingValue(this.parent.Settings.ADMIN_FOLDER.NAME);
      if (currentFolder && this.adminFolder == currentFolder) return;
      const resp = await this.parent.updateFolder({
        newValue: this.adminFolder,
        currentValue: currentFolder
      });
      if (resp.success) {
        switch (resp.data) {
          case 'SUCCESS':
            await this.parent.updateSetting({
              name: this.parent.Settings.ADMIN_FOLDER.NAME,
              value: this.adminFolder
            });
            await this.parent.getAllSettings();
            this.$alert(this.$t('adminFolderChangeMessage'), this.$t('confirmation'), {
              showClose: false,
              confirmButtonText: 'OK',
              callback: action => {
                location.replace(this.parent.constants.APP_BASE_URL + '/' + this.adminFolder);
              }
            });
            break;
          case 'NO-VALUE':
            this.$message.error(this.$t('adminFolderCantBeEmpty'));
            break;
          default:
            this.$message.error(this.$t('anErrorOccured'));
            this.adminFolder = currentFolder;
        }
      }
    }
  }
};