import "core-js/modules/es.array.push.js";
import { sleep } from '@/utils/chronos';
import { WIDGET_CONTEXT } from '@/store/modules/database/widget';
import { VueDraggableNext } from 'vue-draggable-next';
import { mapActions } from 'vuex';
import FormItemData from './FormItemData.vue';
export default {
  name: 'Widgets',
  components: {
    VueDraggableNext,
    FormItemData
  },
  props: {
    data: {
      type: String,
      default: ''
    }
  },
  emits: ['update', 'save'],
  data() {
    return {
      isLoading: false,
      widgets: [],
      collapses: [],
      widgetsData: [],
      initialWidgets: [],
      dataHasChanged: false,
      newDataItem: {
        id: '',
        type: '',
        option: '',
        label_fr: '',
        label_en: '',
        width: 24,
        required: true
      },
      psid: 0,
      scrollTopY: 0,
      scrollAtBottom: false,
      history: [],
      historyIndex: 0,
      canUpdateHistory: true
    };
  },
  watch: {
    data: {
      handler(val, oldVal) {
        this.objectToArray();
      },
      deep: true
    },
    widgets: {
      handler(val, oldVal) {
        this.dataHasChanged = !this.$_.isEqual(this.$_.keyBy(val, 'wid'), this.data);
        this.$emit('update', this.dataHasChanged, this.widgets.length, this.$_.keyBy(this.widgets, 'wid'), this.$_.keyBy(this.widgets.filter(s => s.wid && s.name_fr && s.name_en), 'wid'));
        if (this.dataHasChanged) this.updateHistory(val);
      },
      deep: true
    }
  },
  created() {
    this.objectToArray();
    this.fetchWidgetsData();
  },
  mounted() {
    window.addEventListener('scroll', this.onScroll);
    this.scrollAtBottom = window.innerHeight + window.scrollY >= document.body.scrollHeight;
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.onScroll);
  },
  methods: {
    ...mapActions('widget', ['getAllWidgets']),
    objectToArray() {
      var array = [];
      this.collapses = [];
      if (this.data) {
        this.$_.forIn(this.$_.cloneDeep(this.data), (value, key) => {
          array.push(value);
          this.collapses.push({
            uid: value.uid,
            isCollapsed: true
          });
        });
      }
      this.widgets = this.$_.cloneDeep(array);
      this.initialWidgets = this.$_.cloneDeep(array);
      this.canUpdateHistory = true;
      this.historyIndex = 0;
      this.history = [];
      setTimeout(() => {
        this.scrollToTop();
      }, 500);
    },
    async fetchWidgetsData() {
      this.isLoading = true;
      const resp = await this.getAllWidgets();
      if (resp.data) this.widgetsData = resp.data;
      this.isLoading = false;
    },
    fixIdCase(v, i) {
      this.widgets[i].wid = this.$_.snakeCase(v);
    },
    getCollapse(uid) {
      return this.collapses.find(c => c.uid == uid);
    },
    genDuplicateWid(widSrc, i = 1) {
      const wid = widSrc + '_' + i;
      if (this.widgets.findIndex(w => w.wid == wid) > -1) return this.genDuplicateWid(widSrc, i + 1);else return wid;
    },
    addWidget() {
      const uid = this.$uuidv4();
      this.widgets.push({
        uid,
        wid: '',
        duplicable: false,
        name_fr: '',
        name_en: '',
        fullscreen: false,
        data: []
      });
      this.collapses.push({
        uid,
        isCollapsed: false
      });
      setTimeout(() => {
        this.scrollToBottom();
      }, 500);
    },
    removeWidget(index) {
      const candidateUid = this.widgets[index]?.uid;
      const candidateWidgetsData = this.widgetsData.filter(wd => wd.uid == candidateUid);
      if (candidateWidgetsData.length) {
        this.$confirm(this.$t('deleteWidgetDataQuestion'), this.$t('delete'), {
          confirmButtonText: this.$t('yes'),
          cancelButtonText: this.$t('cancel'),
          type: 'warning'
        }).then(async () => {
          // await this.removeLinkedWidgetsData(candidateWidgetsData)

          this.collapses = this.collapses.filter(c => c.uid != this.widgets[index]?.uid);
          this.widgets.splice(index, 1);
        }).catch(() => {
          //
        });
      } else {
        this.collapses = this.collapses.filter(c => c.uid != this.widgets[index]?.uid);
        this.widgets.splice(index, 1);
      }
    },
    async removeLinkedWidgetsData(linkedData) {
      const mes = this.$message({
        message: this.$t('pleaseWait'),
        duration: 0
      });
      for (let i = 0; i < linkedData.length; i++) {
        await this.deleteItemInDB({
          context: WIDGET_CONTEXT,
          id: linkedData[i].id
        });
      }
      await this.fetchWidgetsData();
      mes.close();
    },
    duplicateWidget(index) {
      const widget = this.$_.cloneDeep(this.widgets[index]);
      widget.wid = this.genDuplicateWid(widget.wid);
      widget.uid = this.$uuidv4();
      this.widgets.push(widget);
      this.collapses.push({
        uid: widget.uid,
        isCollapsed: false
      });
      setTimeout(() => {
        this.scrollToBottom();
      }, 500);
    },
    addWidgetDataRow(i) {
      if (this.widgets[i].data && this.$_.isArray(this.widgets[i].data)) {
        this.widgets[i].data.push([]);
      } else this.widgets[i].data = [[]];
    },
    addWidgetDataItem(i) {
      if (this.widgets[i].data && this.$_.isArray(this.widgets[i].data)) {
        this.widgets[i].data.push(this.$_.cloneDeep(this.newDataItem));
      } else this.widgets[i].data = [this.$_.cloneDeep(this.newDataItem)];
    },
    updateWidgetDataItem(i, y, newData) {
      this.widgets[i].data[y] = newData;
    },
    removeWidgetDataItem(i, y) {
      this.widgets[i].data.splice(y, 1);
    },
    addWidgetDataSubItem(i, y) {
      if (this.widgets[i].data[y] && this.$_.isArray(this.widgets[i].data[y])) {
        this.widgets[i].data[y].push(this.$_.cloneDeep(this.newDataItem));
      } else this.widgets[i].data[y] = [this.$_.cloneDeep(this.newDataItem)];
    },
    updateWidgetDataSubItem(i, y, z, newData) {
      this.widgets[i].data[y][z] = newData;
    },
    removeWidgetDataSubItem(i, y, z) {
      this.widgets[i].data[y].splice(z, 1);
    },
    toggleCollapses() {
      this.collapses = this.collapses.map(c => ({
        uid: c.uid,
        isCollapsed: this.collapses.findIndex(c => c.isCollapsed) == -1
      }));
    },
    onScroll(e) {
      this.scrollTopY = e.target.documentElement.scrollTop;
      this.scrollAtBottom = window.innerHeight + window.scrollY >= document.body.scrollHeight;
      if (this.scrollTopY < 75) this.psid = 0;
    },
    scrollToBottom() {
      window.scrollTo({
        top: document.body.scrollHeight,
        behavior: 'smooth'
      });
    },
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    },
    scrollToNext() {
      if (this.psid + 1 < this.widgets.length && !this.scrollAtBottom) this.psid++;
      this.scrollToWidget();
    },
    scrollToPrevious() {
      if (this.psid > 0) this.psid--;
      this.scrollToWidget();
    },
    scrollToWidget() {
      const openedNb = this.collapses.filter(c => c.isCollapsed).length;
      const closedNb = this.collapses.filter(c => !c.isCollapsed).length;
      if (openedNb > 0 && closedNb > 0) {
        if (openedNb > closedNb) {
          this.collapses = this.collapses.map(c => ({
            uid: c.uid,
            isCollapsed: true
          }));
        } else {
          this.collapses = this.collapses.map(c => ({
            uid: c.uid,
            isCollapsed: false
          }));
        }
        sleep(500);
      }
      this.$smoothScroll({
        scrollTo: document.getElementById('wid--' + this.psid),
        updateHistory: false,
        duration: 1000,
        offset: -150
      });
    },
    updateHistory(newVal) {
      if (this.canUpdateHistory) {
        this.history.length = this.historyIndex + 1;
        this.history.push(this.$_.cloneDeep(newVal));
        this.historyIndex++;
      }
      this.canUpdateHistory = true;
    },
    undo() {
      if (this.historyIndex > 0) {
        this.canUpdateHistory = false;
        this.historyIndex--;
        if (this.historyIndex == 0) this.widgets = this.$_.cloneDeep(this.initialWidgets);else this.widgets = this.$_.cloneDeep(this.history[this.historyIndex]);
      }
    },
    redo() {
      if (this.historyIndex + 1 < this.history.length) {
        this.canUpdateHistory = false;
        this.historyIndex++;
        this.widgets = this.$_.cloneDeep(this.history[this.historyIndex]);
      }
    },
    async save() {
      for (let i = 0; i < this.widgetsData.length; i++) {
        const wd = this.widgetsData[i];
        const widget = this.widgets.find(w => w.uid == wd.uid);
        if (widget) {
          if (widget.wid != wd.wid) {
            await this.updateItemInDB({
              context: WIDGET_CONTEXT,
              id: wd.id,
              data: {
                wid: widget.wid
              }
            });
          }
        } else {
          await this.deleteItemInDB({
            context: WIDGET_CONTEXT,
            id: wd.id
          });
        }
      }
      await this.fetchWidgetsData();
      this.$emit('save');
    }
  }
};