import { downloadObjectAsJson } from '@/utils/functions';
import { useTemplateStore } from '@/stores/template';
import { mapWritableState } from 'pinia';
export default {
  name: 'JsonEditor',
  data() {
    return {
      index: 0,
      jsonData: {},
      isLoading: false,
      displayInfo: false,
      reactToIndexChange: true,
      widgetFormItemModel: [{
        id: 0,
        label: `[array]`,
        children: [{
          id: 1,
          label: `FORM_ITEM`,
          children: [{
            id: 2,
            label: `id`,
            children: [{
              label: `type: [string]`
            }, {
              label: `description: ${this.$t('id')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }, {
            id: 3,
            label: `type`,
            children: [{
              label: `type: [string]`
            }, {
              label: `description: ${this.$t('type')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }, {
              label: `values: text, text-multilingual, textarea, textarea-multilingual, number, image, image-gallery, html, html-multilingual, checkbox, video, file, color, date, list, slug, select, multiselect, linkedwidget, e-commerce-single-product, e-commerce-multiple-products, e-commerce-single-category, e-commerce-multiple-categories, news-single-article, news-multiple-articles, news-single-category, news-multiple-categories`
            }]
          }, {
