            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }, {
            id: 6,
            label: `width`,
            children: [{
              label: `type: [number]`
            }, {
              label: `description: ${this.$t('itemWidth')}`
            }, {
              label: `is_required: ${this.$t('no')}`
            }, {
              label: `values: 1 - 24`
            }]
          }, {
            id: 7,
            label: `required`,
            children: [{
              label: `type: [boolean]`
            }, {
              label: `description: ${this.$t('itemIsRequired')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }]
        }]
      }],
      widgetObjectModel: [{
        id: 0,
        label: `[object]`,
        children: [{
          id: 1,
          label: `WIDGET`,
          children: [{
            id: 2,
            label: `uid`,
            children: [{
              label: `type: [string]`
            }, {
              label: `description: ${this.$t('uniqueId')} | ${this.$t('mustBeUniqueAlphanumericString')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }, {
            id: 3,
            label: `wid`,
            children: [{
              label: `type: [string]`
            }, {
              label: `description: ${this.$t('id')} | ${this.$t('mustBeInSnakeCase')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }, {
            id: 4,
            label: `duplicable`,
            children: [{
              label: `type: [boolean]`
            }, {
              label: `description: ${this.$t('widgetIsDuplicable')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }, {
            id: 5,
            label: `name_fr`,
            children: [{
              label: `type: [string]`
            }, {
              label: `description: ${this.$t('nameInFrench')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }, {
            id: 6,
            label: `name_en`,
            children: [{
              label: `type: [string]`
            }, {
              label: `description: ${this.$t('nameInEnglish')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }, {
            id: 7,
            label: `fullscreen`,
            children: [{
              label: `type: [boolean]`
            }, {
              label: `description: ${this.$t('fullscreen')}`
            }, {
              label: `is_required: ${this.$t('no')}`
            }]
          }, {
            id: 8,
            label: `data`,
            children: [{
              label: `type: [ array[object], array[array] ]`
            }, {
              label: `description: ${this.$t('formData')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }]
        }]
      }],
      sectionObjectModel: [{
        id: 0,
        label: `[object]`,
        children: [{
          id: 1,
          label: `SECTION`,
          children: [{
            id: 2,
            label: `uid`,
            children: [{
              label: `type: [string]`
            }, {
              label: `description: ${this.$t('uniqueId')} | ${this.$t('mustBeUniqueAlphanumericString')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }, {
            id: 3,
            label: `sid`,
            children: [{
              label: `type: [string]`
            }, {
              label: `description: ${this.$t('id')} | ${this.$t('mustBeInSnakeCase')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }, {
            id: 4,
            label: `name_fr`,
            children: [{
              label: `type: [string]`
            }, {
              label: `description: ${this.$t('nameInFrench')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }, {
            id: 5,
            label: `name_en`,
            children: [{
              label: `type: [string]`
            }, {
              label: `description: ${this.$t('nameInEnglish')}`
            }, {
              label: `is_required: ${this.$t('yes')}`
            }]
          }]
        }]
      }],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      parent: this.$parent.$parent.$parent
    };
  },
  computed: {
    ...mapWritableState(useTemplateStore, ['codeEditorIndex']),
    dataUnchanged() {
      if (this.index) {
        return this.$_.isEqual(this.jsonData, this.widgetsJSONData);
      } else {
        return this.$_.isEqual(this.jsonData, this.sectionsJSONData);
      }
    }
  },
  watch: {
    index: {
      handler(val, oldVal) {
        if (this.reactToIndexChange) this.switchJSONData(oldVal);
        this.reactToIndexChange = true;
        this.codeEditorIndex = val;
      }
    }
  },
  async created() {
    this.index = this.codeEditorIndex;
    this.isLoading = true;
    await this.loadJSONData();
    this.isLoading = false;
    this.jsonData = this.index ? this.widgetsJSONData : this.sectionsJSONData;
  },
  methods: {
    switchJSONData(oldIndex) {
      if (oldIndex) {
        if (!this.$_.isEqual(this.jsonData, this.widgetsJSONData)) {
          this.confirmSwitching(this.sectionsJSONData, oldIndex);
        } else {
          this.jsonData = this.sectionsJSONData;
        }
      } else {
        if (!this.$_.isEqual(this.jsonData, this.sectionsJSONData)) {
          this.confirmSwitching(this.widgetsJSONData, oldIndex);
        } else {
          this.jsonData = this.widgetsJSONData;
        }
      }
    },
    confirmSwitching(data, oldIndex) {
      this.$confirm(this.$t('discardChanges'), this.$t('confirm'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        this.jsonData = data;
      }).catch(() => {
        this.reactToIndexChange = false;
        this.index = oldIndex;
      });
    },
    onJSONDataChange(updatedContent, previousContent, patchResult) {
      // console.log('onChange', updatedContent, previousContent, patchResult)
    },
    async saveJSONData() {
      const task = await this.parent.updateJSON({
        data: this.formatDataKeys(this.jsonData),
        file: this.index ? 'widgets' : 'sections'
      });
      if (task.success && task.data) {
        await this.loadJSONData();
        this.jsonData = this.index ? this.widgetsJSONData : this.sectionsJSONData;
        this.$message.success(this.$t('success'));
      } else {
        this.$message.error(this.$t('anErrorOccured'));
      }
    },
    formatDataKeys(data) {
      const result = {};
      this.$_.forIn(data, (value, key) => {
        const clone = this.$_.cloneDeep(value);
        if (this.index) {
          clone.wid = this.$_.snakeCase(key);
          result[clone.wid] = clone;
        } else {
          clone.sid = this.$_.snakeCase(key);
          result[clone.sid] = clone;
        }
      });
      return result;
    },
    copyJsonData(data, success) {
      if (success) this.$message.success(this.$t('success'));
    },
    downloadJsonData() {
      downloadObjectAsJson(this.index ? this.widgetsJSONData : this.sectionsJSONData, this.index ? 'widgets' : 'sections');
    }
  }
};