import "core-js/modules/es.array.push.js";
import { PAGE_CONTEXT } from '@/store/modules/database/page';
import { VueDraggableNext } from 'vue-draggable-next';
import { mapActions } from 'vuex';
export default {
  name: 'Sections',
  components: {
    VueDraggableNext
  },
  props: {
    data: {
      type: String,
      default: ''
    }
  },
  emits: ['update', 'save'],
  data() {
    return {
      isLoading: false,
      pages: [],
      sections: [],
      initialSections: [],
      dataHasChanged: false,
      history: [],
      historyIndex: 0,
      canUpdateHistory: true
    };
  },
  watch: {
    data: {
      handler(val, oldVal) {
        this.objectToArray();
      },
      deep: true
    },
    sections: {
      handler(val, oldVal) {
        this.dataHasChanged = !this.$_.isEqual(this.$_.keyBy(val, 'sid'), this.data);
        this.$emit('update', this.dataHasChanged, this.sections.length, this.$_.keyBy(this.sections, 'sid'), this.$_.keyBy(this.sections.filter(s => s.sid && s.name_fr && s.name_en && typeof s.news_categories == 'number' && typeof s.e_commerce_categories == 'number'), 'sid'));
        if (this.dataHasChanged) this.updateHistory(val);
      },
      deep: true
    }
  },
  created() {
    this.objectToArray();
    this.fetchPages();
  },
  methods: {
    ...mapActions('page', ['getAllPages']),
    objectToArray() {
      const array = [];
      if (this.data) {
        this.$_.forIn(this.$_.cloneDeep(this.data), function (value, key) {
          array.push(value);
        });
      }
      this.sections = this.$_.cloneDeep(array);
      this.initialSections = this.$_.cloneDeep(array);
      this.canUpdateHistory = true;
      this.historyIndex = 0;
      this.history = [];
    },
    async fetchPages() {
      this.isLoading = true;
      const resp = await this.getAllPages();
      if (resp.data) this.pages = resp.data;
      this.isLoading = false;
    },
    addSection() {
      this.sections.push({
        uid: this.$uuidv4(),
        sid: '',
        name_fr: '',
        name_en: '',
        news_categories: 0,
        e_commerce_categories: 0
      });
    },
    removeSection(index) {
      const pagesWhereSectionIsUsed = [];
      const candidateUid = this.sections[index]?.uid;
      this.pages.forEach(page => {
        let topSections, bottomSections;
        try {
          topSections = JSON.parse(page.top_sections);
        } catch (e) {
          topSections = [];
        }
        try {
          bottomSections = JSON.parse(page.bottom_sections);
        } catch (e) {
          bottomSections = [];
        }
        if (topSections.includes(candidateUid)) {
          pagesWhereSectionIsUsed.push({
            id: page.id,
            placement: 'top',
            list: topSections.filter(s => s != candidateUid)
          });
        }
        if (bottomSections.includes(candidateUid)) {
          pagesWhereSectionIsUsed.push({
            id: page.id,
            placement: 'bottom',
            list: bottomSections.filter(s => s != candidateUid)
          });
        }
      });
      if (pagesWhereSectionIsUsed.length) {
        this.$confirm(this.$t('deleteSectionQuestion'), this.$t('delete'), {
          confirmButtonText: this.$t('yes'),
          cancelButtonText: this.$t('cancel'),
          type: 'warning'
        }).then(async () => {
          await this.removeSectionFromPages(pagesWhereSectionIsUsed);
          this.sections.splice(index, 1);
        }).catch(() => {
          /* empty */
        });
      } else this.sections.splice(index, 1);
    },
    async removeSectionFromPages(pages) {
      const mes = this.$message({
        message: this.$t('pleaseWait'),
        duration: 0
      });
      for (let i = 0; i < pages.length; i++) {
        const data = pages[i].placement == 'top' ? {
          top_sections: JSON.stringify(pages[i].list)
        } : {
          bottom_sections: JSON.stringify(pages[i].list)
        };
        await this.updateItemInDB({
          context: PAGE_CONTEXT,
          id: pages[i].id,
          data
        });
      }
      await this.fetchPages();
      mes.close();
    },
    fixIdCase(v, i) {
      this.sections[i].sid = this.$_.snakeCase(v);
    },
    updateHistory(newVal) {
      if (this.canUpdateHistory) {
        this.history.length = this.historyIndex + 1;
        this.history.push(this.$_.cloneDeep(newVal));
        this.historyIndex++;
      }
      this.canUpdateHistory = true;
    },
    undo() {
      if (this.historyIndex > 0) {
        this.canUpdateHistory = false;
        this.historyIndex--;
        if (this.historyIndex == 0) this.sections = this.$_.cloneDeep(this.initialSections);else this.sections = this.$_.cloneDeep(this.history[this.historyIndex]);
      }
    },
    redo() {
      if (this.historyIndex + 1 < this.history.length) {
        this.canUpdateHistory = false;
        this.historyIndex++;
        this.sections = this.$_.cloneDeep(this.history[this.historyIndex]);
      }
    }
  }
};