import "core-js/modules/es.array.push.js";
import AccessManager from '@/mixins/access-manager';
import ViewsBaseMixin from '@/mixins/views-base';
import { mapActions } from 'vuex';
export default {
  name: 'Medias',
  mixins: [ViewsBaseMixin, AccessManager],
  data() {
    return {
      GD: false,
      IMAGICK: false,
      thumbnails: [],
      thumbnailFiles: [],
      customThumbnails: [],
      thumbnailsFormat: 0,
      compressThreshold: 0,
      compressImages: false,
      isGenerating: false,
      imgProcessingLib: '',
      displayTrash: false,
      keepDeletedFiles: false,
      disableDisplayTrash: false,
      thumbnailsFolder: 'thumbnails',
      mediasFolder: 'medias',
      parent: this.$parent.$parent.$parent
    };
  },
  watch: {
    thumbnails: {
      handler(val, oldVal) {
        this.updateCustomThumbnails();
      },
      deep: true
    }
  },
  created() {
    this.init();
  },
  methods: {
    ...mapActions('media', ['checkLib', 'getFolderMedias', 'generateThumbnail', 'generateThumbnails']),
    async init() {
      this.displayTrash = !!this.parent.getSettingValue(this.parent.Settings.DISPLAY_TRASH.NAME, this.parent.VarTypes.NUMBER);
      this.compressImages = !!this.parent.getSettingValue(this.parent.Settings.COMPRESS_IMAGES.NAME, this.parent.VarTypes.NUMBER);
      this.compressThreshold = this.parent.getSettingValue(this.parent.Settings.COMPRESS_THRESHOLD.NAME, this.parent.VarTypes.NUMBER);
      this.keepDeletedFiles = !!this.parent.getSettingValue(this.parent.Settings.KEEP_DELETED_FILES.NAME, this.parent.VarTypes.NUMBER);
      this.thumbnailsFormat = this.parent.getSettingValue(this.parent.Settings.THUMBNAILS_FORMAT.NAME, this.parent.VarTypes.NUMBER);
      this.customThumbnails = this.parent.getSettingValue(this.parent.Settings.CUSTOM_THUMBNAILS.NAME, this.parent.VarTypes.ARRAY);
      this.imgProcessingLib = this.parent.getSettingValue(this.parent.Settings.IMAGE_PROCESSING_LIBRARY.NAME);
      this.thumbnails = this.parent.defaultThumbnails;
      this.customThumbnails.forEach(thumbnail => {
        this.thumbnails.push(thumbnail);
      });
      this.disableDisplayTrash = !this.keepDeletedFiles;
      this.checkProcessingLibs();
      this.loadThumbnailFiles();
    },
    async checkProcessingLibs() {
      this.GD = (await this.checkLib({
        lib: 'gd'
      })).data;
      this.IMAGICK = (await this.checkLib({
        lib: 'imagick'
      })).data;
    },
    async loadThumbnailFiles() {
      const resp = await this.getFolderMedias({
        folder: this.thumbnailsFolder
      });
      if (resp.success && resp.data) this.thumbnailFiles = resp.data;
    },
    async updateCompressImages(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.COMPRESS_IMAGES.NAME,
        value: value ? 1 : 0
      });
      await this.parent.getAllSettings();
    },
    async updateCompressThreshold(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.COMPRESS_THRESHOLD.NAME,
        value: value
      });
      await this.parent.getAllSettings();
    },
    async updateKeepDeletedFiles(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.KEEP_DELETED_FILES.NAME,
        value: value ? 1 : 0
      });
      await this.parent.getAllSettings();
      this.disableDisplayTrash = false;
      if (!value) {
        this.displayTrash = false;
        await this.updateDisplayTrash(false);
      }
      this.disableDisplayTrash = !this.keepDeletedFiles;
    },
    async updateDisplayTrash(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.DISPLAY_TRASH.NAME,
        value: value ? 1 : 0
      });
      await this.parent.getAllSettings();
    },
    async updateThumbnailsFormat(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.THUMBNAILS_FORMAT.NAME,
        value: value
      });
      await this.parent.getAllSettings();
    },
    async updateImgProcessingLib(value) {
      await this.parent.updateSetting({
        name: this.parent.Settings.IMAGE_PROCESSING_LIBRARY.NAME,
        value: value
      });
      await this.parent.getAllSettings();
    },
    async updateCustomThumbnails() {
      if (this.thumbnails.length >= 4) {
        const ct = [];
        this.thumbnails.forEach((thumbnail, i) => {
          if (i > 3 && thumbnail.label && thumbnail.width && thumbnail.height) ct.push(thumbnail);
        });
        await this.parent.updateSetting({
          name: this.parent.Settings.CUSTOM_THUMBNAILS.NAME,
          value: ct.length > 0 ? JSON.stringify(ct) : ''
        });
        await this.parent.getAllSettings();
      }
    },
    addThumbnail() {
      this.thumbnails.push({
        label: '',
        width: 1,
        height: 1,
        editable: true
      });
    },
    removeThumbnail(index) {
      this.thumbnails.splice(index, 1);
    },
    async genThumbnailFiles() {
      if (!this.imgProcessingLib) {
        this.$message.error(this.$t('noLibSelected'));
        return;
      }
      this.isGenerating = true;
      const refreshThumbnailFiles = setInterval(() => {
        this.loadThumbnailFiles();
      }, 500);
      const task = await this.generateThumbnails({
        reset: true,
        autoWidth: this.thumbnailsFormat == this.parent.ThumbnailFormats.AUTOWIDTH.value,
        autoHeight: this.thumbnailsFormat == this.parent.ThumbnailFormats.AUTOHEIGHT.value,
        folder: this.mediasFolder,
        lib: this.imgProcessingLib,
        models: this.thumbnails.filter(thumbnail => thumbnail.label && thumbnail.width && thumbnail.height)
      });
      if (task.success && task.data) this.$message.success(this.$t('success'));else this.$message.error(this.$t('anErrorOccured'));
      clearInterval(refreshThumbnailFiles);
      this.loadThumbnailFiles();
      this.isGenerating = false;
    }
  }
};