import { API_REQUEST_HEADERS, STORAGE_API_BASE_URL } from '@/utils/constants';
import { compressImage, dataUrlToFile, getNameInitials } from '@/utils/functions';
import ViewsBaseMixin from '@/mixins/views-base';
import { USER_CONTEXT } from '@/store/modules/database/user';
import sanitizer from 'string-sanitizer';
import { mapActions, mapMutations, mapState } from 'vuex';
export default {
  name: 'AccountSettings',
  mixins: [ViewsBaseMixin],
  data() {
    return {
      account: {
        uid: '',
        name: '',
        email: '',
        avatar: '',
        password: ''
      },
      isUidValid: true,
      disablePasswordInput: true,
      passwordPlaceholder: '●●●●●●●●●●●●●●',
      currentStoFolder: 'avatars',
      pickerRatio: null,
      pickerVisible: false,
      pickerRoundPreview: true,
      currentPickerLabel: null
    };
  },
  computed: {
    ...mapState({
      userData: state => state.session.userData
    }),
    updateBtnDisabled() {
      return this.userData && this.disablePasswordInput && this.account.uid == this.userData.uid && this.account.name == this.userData.name && this.account.email == this.userData.email;
    }
  },
  watch: {
    'account.uid': 'checkUid'
  },
  created() {
    this.account.uid = this.userData.uid;
    this.account.name = this.userData.name;
    this.account.email = this.userData.email;
    this.account.avatar = this.userData.avatar;
  },
  methods: {
    ...mapMutations({
      updateUserData: 'session/updateUserData'
    }),
    ...mapActions({
      checkUser: 'user/checkUser',
      getUserByCredentials: 'authentication/getUserByCredentials'
    }),
    getNameInitials,
    togglePasswordInput() {
      this.account.password = '';
      this.disablePasswordInput = !this.disablePasswordInput;
      this.passwordPlaceholder = this.disablePasswordInput ? '●●●●●●●●●●●●●●' : this.$t('password');
    },
    async checkUid() {
      this.account.uid = sanitizer.sanitize(this.account.uid.toLowerCase());
      const task = await this.checkUser(this.account.uid);
      this.isUidValid = this.account.uid === '' || this.userData.uid === this.account.uid || task.success && !task.data;
    },
    async updateAccountInfo() {
      if (!this.account.uid) {
        this.$message.error(this.$t('idRequired'));
        return;
      }
      if (!this.account.name) {
        this.$message.error(this.$t('nameRequired'));
        return;
      }
      if (!this.disablePasswordInput && !this.account.password) {
        this.$message.error(this.$t('passwordRequired'));
        return;
      }
      if (!this.disablePasswordInput && this.account.password < 6) {
        this.$message.error(this.$t('shortPassword'));
        return;
      }
      await this.checkUid();
      if (!this.isUidValid) {
        this.$message.error(this.$t('idUnavailable'));
        return;
      }
      if (!this.disablePasswordInput) {
        this.$prompt(this.$t('provideCurrentPassword'), this.$t('confirmation'), {
          inputPlaceholder: this.$t('password'),
          confirmButtonText: this.$t('confirm'),
          cancelButtonText: this.$t('cancel'),
          inputType: 'password'
        }).then(async ({
          value
        }) => {
          if (value) {
            const resp = await this.getUserByCredentials({
              username: this.userData.uid,
              password: value
            });
            if (resp.success) {
              if (resp.data.username && resp.data.password && resp.data.user) {
                this.executeUpdate(true);
              } else {
                this.$message.error(this.$t('wrongPassword'));
              }
            } else {
              this.$message.error(this.$t('anErrorOccured'));
            }
          }
        }).catch(() => {
          /* empty */
        });
      } else {
        this.executeUpdate();
      }
    },
    async executeUpdate(includePassword = false) {
      const newData = {};
      if (this.account.uid !== this.userData.uid) newData['uid'] = this.account.uid;
      if (this.account.name !== this.userData.name) newData['name'] = this.account.name;
      if (this.account.email !== this.userData.email) newData['email'] = this.account.email;
      if (includePassword) newData['password'] = this.account.password;
      if (!this.$_.isEmpty(newData)) {
        const task = await this.updateItemInDB({
          context: USER_CONTEXT,
          id: this.userData.id,
          data: newData
        });
        if (task.success) {
          this.$message.success(this.$t('success'));
          const user = this.userData;
          user.uid = this.account.uid;
          user.name = this.account.name;
          user.email = this.account.email;
          if (includePassword) this.togglePasswordInput();
          this.updateUserData(user);
        } else {
          this.$message.error(this.$t('anErrorOccured'));
        }
      }
    },
    async updateAvatar(value = '') {
      const task = await this.updateItemInDB({
        context: USER_CONTEXT,
        id: this.userData.id,
        data: {
          avatar: value
        }
      });
      if (task.success) {
        this.$message.success(this.$t('success'));
        const user = this.userData;
        user.avatar = value;
        this.updateUserData(user);
      } else {
        this.$message.error(this.$t('anErrorOccured'));
      }
    },
    async saveAvatar(result, dataUrl) {
      var file = dataUrlToFile(dataUrl, {
        filename: this.$uuidv4() + '.png',
        type: 'image/png'
      });
      file = await compressImage(file, {
        maxSizeMB: 1
      });
      const formData = new FormData();
      formData.append('folder', this.currentStoFolder);
      formData.append('file', file);
      this.axios.post(STORAGE_API_BASE_URL, formData, {
        headers: API_REQUEST_HEADERS
      }).then(async res => {
        const path = 'root/' + this.currentStoFolder + '/' + file.name;
        await this.updateAvatar(path);
        this.account.avatar = path;
        this.pickerVisible = false;
      }).catch(err => {
        this.$message.error(this.$t('anErrorOccured'));
      });
    },
    clearAvatar() {
      this.account.avatar = '';
      this.updateAvatar();
    }
  }
};