/* eslint-disable vue/no-mutating-props */
export default {
  name: 'FormItemData',
  props: {
    item: {
      required: true,
      type: Object
    }
  },
  emits: ['remove', 'updateItemData'],
  data() {
    return {
      formItemTypes: [{
        id: 'text',
        label: this.$t('text')
      }, {
        id: 'text-multilingual',
        label: this.$t('text') + ' - ' + this.$t('multilingual')
      }, {
        id: 'textarea',
        label: this.$t('longText')
      }, {
        id: 'textarea-multilingual',
        label: this.$t('longText') + ' - ' + this.$t('multilingual')
      }, {
        id: 'number',
        label: this.$t('number')
      }, {
        id: 'image',
        label: this.$t('image')
      }, {
        id: 'image-gallery',
        label: this.$t('imageGallery')
      }, {
        id: 'video-gallery',
        label: this.$t('videoGallery')
      }, {
        id: 'html',
        label: this.$t('htmlContent')
      }, {
        id: 'html-multilingual',
        label: this.$t('htmlContent') + ' - ' + this.$t('multilingual')
      }, {
        id: 'checkbox',
        label: this.$t('checkbox')
      }, {
        id: 'video',
        label: this.$t('video')
      }, {
        id: 'file',
        label: this.$t('file')
      }, {
        id: 'color',
        label: this.$t('color')
      }, {
        id: 'date',
        label: this.$t('date')
      }, {
        id: 'list',
        label: this.$t('list')
      }, {
        id: 'slug',
        label: this.$t('slug')
      }, {
        id: 'icon',
        label: this.$t('uniqueIcon')
      }, {
        id: 'multipleicon',
        label: this.$t('multipleIcon')
      }, {
        id: 'select',
        label: this.$t('uniqueSelection')
      }, {
        id: 'multiselect',
        label: this.$t('multipleSelection')
      }, {
        id: 'linkedwidget',
        label: this.$t('linkedWidget')
      }, {
        id: 'e-commerce-single-product',
        label: this.$t('eCommerce') + ' - ' + this.$t('singleProduct')
      }, {
        id: 'e-commerce-multiple-products',
        label: this.$t('eCommerce') + ' - ' + this.$t('multipleProducts')
      }, {
        id: 'e-commerce-single-category',
        label: this.$t('eCommerce') + ' - ' + this.$t('singleCategory')
      }, {
        id: 'e-commerce-multiple-categories',
        label: this.$t('eCommerce') + ' - ' + this.$t('multipleCategories')
      }, {
        id: 'news-single-article',
        label: this.$t('news') + ' - ' + this.$t('singleArticle')
      }, {
        id: 'news-multiple-articles',
        label: this.$t('news') + ' - ' + this.$t('multipleArticles')
      }, {
        id: 'news-single-category',
        label: this.$t('news') + ' - ' + this.$t('singleCategory')
      }, {
        id: 'news-multiple-categories',
        label: this.$t('news') + ' - ' + this.$t('multipleCategories')
      }],
      iconLibraries: ['antd', 'carbon', 'fa', 'fluent', 'ionicons4', 'ionicons5', 'material', 'tabler']
    };
  },
  computed: {
    selectorItemOption: {
      get() {
        let value;
        try {
          value = JSON.parse(this.item.option);
        } catch (e) {
          value = [];
        }
        return value;
      },
      set(value) {
        this.item.option = JSON.stringify(value);
      }
    },
    includeSubCategoriesOption: {
      get() {
        if (!this.item.option) this.item.option = '1';
        return this.item.option;
      },
      set(value) {
        this.item.option = value;
      }
    },
    linkedWidgetItemOptionWid: {
      get() {
        let value;
        try {
          value = JSON.parse(this.item.option)?.wid;
        } catch (e) {
          value = '';
        }
        return value;
      },
      set(value) {
        const v = {
          wid: this.$_.snakeCase(value),
          label: this.linkedWidgetItemOptionLabel
        };
        this.item.option = JSON.stringify(v);
      }
    },
    linkedWidgetItemOptionLabel: {
      get() {
        let value;
        try {
          value = JSON.parse(this.item.option)?.label;
        } catch (e) {
          value = '';
        }
        return value;
      },
      set(value) {
        const v = {
          wid: this.linkedWidgetItemOptionWid,
          label: this.$_.snakeCase(value)
        };
        this.item.option = JSON.stringify(v);
      }
    }
  },
  watch: {
    item: {
      handler(val, oldVal) {
        this.$emit('updateItemData', val);
      },
      deep: true
    }
  },
  methods: {
    fixCase(v, el) {
      this.item[el] = this.$_.snakeCase(v);
    }
  }
};
/* eslint-enable vue/no-mutating-props */